import Vue from 'vue';
import Router from 'vue-router';
// Containers
const DefaultContainer = () => import('@/containers/DefaultContainer');

// Views
const Apartment = () => import('@/views/apartment/Apartment');
const ApartmentDetail = () => import('@/views/apartment/Detail');
const ApartmentCreate = () => import('@/views/apartment/Create');

Vue.use(Router);

export default new Router({
  mode: 'history', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/apartment/table',
      name: 'Home',
      component: DefaultContainer,
      children: [
        {
          path: '/apartment/table',
          name: 'table-apartment',
          component: Apartment
        },
        {
          path: '/apartment/create',
          name: 'create-apartment',
          component: ApartmentCreate
        },
        {
          path: '/apartment/:id',
          name: 'detail-apartment',
          component: ApartmentDetail
        },
      ]
    },
  ]
})
